.ufu_application {
  border-radius: var(--radius-6);
  border: 1px solid var(--border-color-1);
  background-color: var(--bg-color-white);
  margin-block-end: 0.9rem;
  cursor: pointer;
}
.ufu_your_application {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  padding: 1rem;
}
.ufu_your_application > img {
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
}
.ufu_your_application_inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.2rem;
  width: 100%;
}
.ufu_your_application_inner > h6 {
  font-weight: 500;
  font-size: 0.9rem;
  line-height: var(--line-15);
  color: var(--color-input);
  margin: 0;
}
.ufu_your_application_inner > p {
  font-weight: 400;
  font-size: 0.7rem;
  line-height: var(--line-15);
  color: var(--color-input-placeholder);
  margin: 0;
}
.ufu_application_form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-inline: 1rem;
  cursor: pointer;
  border-bottom-left-radius: var(--radius-4);
  border-bottom-right-radius: var(--radius-4);
  background-color: var(--bg-light-green);
}
.ufu_application_form > h6 {
  font-weight: 400;
  font-size: 0.8rem;
  line-height: var(--line-15);
  color: var(--color-input-label);
  padding-block: 0.5rem;
  margin: 0;
}
.ufu_application_form > img {
  height: 0.8rem;
  width: auto;
}
/* // for admission page style  */
.yaclp_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  padding: 1rem;
  border-radius: 6px;
  background-color: #fff;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 255, 0.25));
  margin-bottom: 0.8rem;
  cursor: pointer;
}
.yaclp_container > img,
.yap_container > img,
.yap_container_other > img,
.yap_container_status > img {
  width: auto;
  height: 2rem;
}
.yap_container {
  padding: 1rem;
  border-radius: 6px;
  background-color: #fff;
  padding: 1rem;
  border-radius: 6px;
  filter: drop-shadow(0px 4px 4px #002bbe);
  margin-bottom: 0.8rem;
}
.yap_container > h6 {
  font-weight: 400;
  font-size: 0.8rem;
  line-height: var(--line-15);
  color: var(--color-input-label);
  margin: 0;
}
.yap_container > p {
  font-weight: 400;
  font-size: 0.7rem;
  line-height: var(--line-15);
  color: var(--color-input-placeholder);
  margin: 0;
}
.yap_container_other {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 1rem;
  padding: 1rem;
  border-radius: 6px;
  border: 0.5px solid rgba(0, 0, 0, 0.2);
  background-color: #fff;
  padding: 1rem;
  border-radius: 6px;
  margin-bottom: 0.8rem;
}
.yap_container_status {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 1rem;
  padding: 1rem;
  border-radius: 6px;
  background-color: #fff;
  padding: 1rem;
  border-radius: 6px;
  filter: drop-shadow(0px 4px 4px #002bbe);
  margin-bottom: 0.8rem;
}
/* // ui of all status process  */
.as_container {
  padding: 1.5rem;
  border-radius: 2px;
  border: 0.5px solid rgba(0, 0, 0, 0.2);
  background: #fff;
  cursor: pointer;
  min-height: fit-content;
}
.as_sub_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}
.as_container_inner {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1.3rem;
}
.as_container_inner > img,
.as_container > img {
  width: auto;
  height: 1.5rem;
}
.as_container_inner > h6 {
  color: rgba(18, 18, 18, 0.8);
  font-size: 14px;
  font-weight: 700;
  margin: 0;
}
.as_another_container {
  padding: 0.5rem 2.8rem;
}
.as_another_container > p,
.as_another_container > a {
  font-weight: 400;
  font-size: 0.9rem;
  line-height: var(--line-15);
  color: rgb(18 18 18 / 70%);
  margin: 0;
  margin-bottom: 0.3rem;
  letter-spacing: 0.1px;
}
.as_another_container > a > img {
  margin-left: 0.3rem;
  width: auto;
  height: 0.8rem;
  cursor: pointer;
}
